html {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  font-size: 16px;
  background: theme("colors.mono-50");
}

body {
  -moz-osx-font-smoothing: grayscale;
  -ms-overflow-style: none;
  -webkit-font-smoothing: antialiased;
  position: relative;

  &::-webkit-scrollbar,
  *::-webkit-scrollbar {
    display: none;
  }
}

//

* {
  box-sizing: border-box;
}

a,
a:hover,
a:visited {
  color: inherit;
  text-decoration: none;
  appearance: none;
}

button {
  outline: none;
  appearance: none;
}

button:focus,
input:focus,
textarea:focus {
  outline: none;
}

input:not[type="checkbox"] {
  appearance: none;
}

input {
  &::-webkit-input-placeholder {
    color: "black" !important;
  }

  &::-moz-placeholder {
    color: "black" !important;
  }

  &:-ms-input-placeholder {
    color: "black" !important;
  }

  &:-moz-placeholder {
    color: "black" !important;
  }
}

textarea {
  resize: none;
}

.square {
  width: 100%;
  position: relative;
  padding-bottom: 100%;
  overflow: hidden;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  position: absolute !important;
  overflow: hidden;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  word-wrap: normal !important;
}

input[type="number"] {
  -moz-appearance: textfield;
}

// hide back to top on Galaxy devices
#scroll-btn {
  display: none !important;
}
